import uploadimgs from "@/components/upload-image/upload-imgs.vue"
export default {
	components: {
		uploadimgs
	},
	data() {
		return {
			leibie: '朋友圈推广',
			contenttext: '',
			task_type_id: 3,
			title: '',
			scope_day: 0,
			publish_count: 0,
			publish_price: '0',
			url: ''
		}
	},
	methods: {
		countjian() {
			if (this.publish_count > 0) {
				this.publish_count--;
			}
		},
		countjia() {
			this.publish_count++;
		},
		dayjian() {
			if (this.scope_day > 0) {
				this.scope_day--;
			}
		},
		dayjia() {
			this.scope_day++;
		},
		release(){
			if(this.publish_count<1){
				this.util.showWindow("任务数量不能为0");
				return;
			}
			let params = {
				"content": this.contenttext,
				"task_images": this.util.uploadImgas,
				"task_type_id":this.task_type_id,
				"title": this.title,
				"scope_day": this.scope_day,
				"publish_count": this.publish_count,
				"publish_price": this.publish_price,
				"url": this.url
			};
			let url="/api/task/add";
			this.util.request(url,"POST",params,(res) => {
				if(res.statusCode==200){
					if(res.data.code==1){
						this.util.showWindow("发布成功");
						uni.switchTab({
							url:"../../task/index/index"
						})
					}else{
						this.util.showWindow("注册失败");
					}
				}else{
					this.util.showWindow("注册请求失败");
				}
			});
		}
	}
}
